
/*---------------------------------------
    TOP NAV
-----------------------------------------*/

.logo-text {
	font-family: 'Roboto', 'open-sans';
	font-size: 2.5em;
	font-weight: 900;
	padding: 1.25em 0 1.25em 1.25em;
    -o-padding-before: 1.25em;
    -o-padding-after: 1.25em;
    -moz-padding-before: 1.25em;
    -moz-padding-after: 1.25em;
    -webkit-padding-before: 1.25em;
    -webkit-padding-after: 1.25em;
}

.nav-Active {
    display: -ms-flexbox;
    display: flex;
    background: #f3f3f3;
    padding: 15px 0px;
}

.logo-box {
    width: 80%;
    padding: 9px 0px 9px 20px;
}

.logo-image {
    width: 21%;
}

.user-icon-box {
    display: -ms-flexbox;
    display: flex;
}
 
.user-icon-name {
    padding: 20px 10px 10px 10px;
}
.user-icon-image a img{
    background: #000;
    border-radius: 50%;
}


/*---------------------------------------
    SIDE BAR
-----------------------------------------*/

.side-bar {
    color: #000;
    height: 100vh;
    min-height: 100vh;
    background: #ffffff;
}

.sidebar-item {
    display: block;
    padding: 0.5em 0 0.5em 0.5em 
}

.sidebar-item img {
    background: #666;
}

.sidebar-item-active, .sidebar-item-active a :visited :active :hover :focus {
    color: #000 !important;
    font-weight: 600;
    text-decoration: none !important;
}

.sidebar-item-active img {
    background: #000 !important;
}






